import Cookie from 'js-cookie';
import router from '../router';
export function logout() {
    if (Cookie.getJSON('loggedIn')) {
        Cookie.remove('loggedIn');
    }
    router.push('/');
}
export var TANK_STATUS;
(function (TANK_STATUS) {
    TANK_STATUS["BUSY"] = "busy";
    TANK_STATUS["BROKEN"] = "broken";
    TANK_STATUS["AVAILABLE"] = "available";
    TANK_STATUS["BREWING"] = "brewing";
    TANK_STATUS["TRANSFERRING"] = "transferring";
    TANK_STATUS["COMPLETED"] = "completed";
})(TANK_STATUS || (TANK_STATUS = {}));
export var ACTION;
(function (ACTION) {
    ACTION[ACTION["PRIMARY_FERMENTATION"] = 1] = "PRIMARY_FERMENTATION";
    ACTION[ACTION["EXCEPTION"] = 6] = "EXCEPTION";
})(ACTION || (ACTION = {}));
