import Vue from 'vue';
import c3 from 'c3';
import 'c3/c3.min.css';
export default Vue.extend({
    name: 'chart',
    props: ['title', 'date', 'data'],
    data: function () {
        return {};
    },
    watch: {
        data: function () {
            this.data.unshift(this.title);
            this.date.unshift('Date');
            c3.generate({
                bindto: this.$el,
                data: {
                    x: 'Date',
                    xFormat: '%m/%d/%Y %H:%M',
                    columns: [this.date, this.data]
                },
                axis: {
                    x: {
                        type: 'timeseries',
                        tick: {
                            format: '%m/%d %H:%M'
                        }
                    },
                    y: {
                        tick: {
                            format: function (d) {
                                return +d.toFixed(10);
                            }
                        }
                    }
                },
                zoom: {
                    enabled: true
                }
            });
        }
    }
});
