import Vue from 'vue';
import Cookie from 'js-cookie';
import router from '../router/index.js';
export default Vue.extend({
    name: 'navbar',
    data: function () {
        return {
            admin: false
        };
    },
    props: ['activeState'],
    beforeMount: function () {
        this.admin = Cookie.getJSON('loggedIn').admin;
    },
    methods: {
        home: function () {
            if (!Cookie.getJSON('loggedIn')) {
                router.push('/');
            }
            else {
                router.push('/home');
            }
        },
        batchHistory: function () {
            if (!Cookie.getJSON('loggedIn')) {
                router.push('/');
            }
            else {
                router.push('/batch-history');
            }
        },
        tankHistory: function () {
            if (!Cookie.getJSON('loggedIn')) {
                router.push('/');
            }
            else {
                router.push('/tank-history');
            }
        },
        adminPage: function () {
            if (!Cookie.getJSON('loggedIn')) {
                router.push('/');
            }
            else {
                router.push('/admin');
            }
        }
    }
});
