import Vue from 'vue';
import router from '../../router/index.js';
import Cookie from 'js-cookie';
export default Vue.extend({
    name: 'recipe',
    props: {
        recipe: {
            type: Object,
            required: true
        },
        volume: {
            type: Number,
            required: true
        }
    },
    beforeMount: function () {
        if (!Cookie.getJSON('loggedIn')) {
            router.push('/');
        }
    }
});
